const { CartActionTypes } = require("./cart.actions");

const INITIAL_STATE = {
  items: {},
};

/**
 * Reducer for the shopping cart state
 * @param {*} state
 * @param {*} action
 */
const cartReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case CartActionTypes.AddItemToCart:
      return {
        ...state,
      };

    default:
      return state;
  }
};

export default cartReducer;
